<template>
  <page
    @on-ready="onReady"
    :headerHeightVW="headerHeightVW"
    :scrollbar="true"
    ref="page"
    class="device-page"
  >

    <template slot="header">
      <van-nav-bar id="header" title="选择设备" :left-arrow="(types && types.length) ? true : false" @click-left="onClickLeft">
      </van-nav-bar>

      <div class="search-bar">

        <van-search
          placeholder="请输入搜索关键词"
          show-action
          shape="round"
          v-model="query"
        >
          <div slot="action" @click="onSearch">搜索</div>
        </van-search>
      </div>
    </template>

    <div class="list-container">

      <template v-if="!queryShow">
        <van-cell-group>
          <van-cell v-if="typeShow"><span style="color: rgba(0,0,0,0.5);" v-html="typeShow"></span></van-cell>
        </van-cell-group>

        <van-cell-group>
          <van-radio-group v-model="radio">
          <van-cell @click="handleType(item)" v-for="item in typeList" :key="item.id"  is-link clickable>
            <!-- <van-button type="primary" size="mini" color="#1b82d2">选择</van-button> -->
            <van-radio slot="icon" :name="item.id" @click.stop="handleTypeSelect(item)" v-if="showRadio">&nbsp;&nbsp;</van-radio>
            <span>{{item.name}}</span>
          </van-cell>
          </van-radio-group>
        </van-cell-group>
        

        <van-cell-group>
          <van-cell @click="handleDevice(item)" v-for="item in deviceList" :key="item.id" :title="item.name"  clickable>
            <span>{{item.serial}}</span>
          </van-cell>
        </van-cell-group>
      </template>

      <template v-else>
        <van-cell-group>
          <van-cell @click="handleDevice(item)" v-for="item in queryList" :key="item.id" :title="item.name"  clickable>
            <span>{{item.serial}}</span>
            <span slot="label">{{item.typeName}}</span>
          </van-cell>
        </van-cell-group>
      </template>
    </div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    deviceList: [],
    typeList: [],
    types: [{name: '设备', id: 0}],
    id: null,
    mode: null,
    radio: null,
    showRadio: true,
    query: '',
    queryShow: false,
    queryList: [],
    profession: null
  }),
  computed: {
    typeShow () {
      let strArray = []
      if (this.types) {
        for (let i = 0; i < this.types.length; i++) {
          strArray.push(this.types[i].name)
        }
      }
      
      strArray[strArray.length - 1] = `<span style="color: #108EE9;">${strArray[strArray.length - 1]}</span>`
      return strArray.join(' > ')
    }
  },
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 57) * 100 / header.w

      if (navigate.getParam('types')) {
        this.types = navigate.getParam('types')
      }

      if (navigate.getParam('category')) {
        this.showRadio = false
      }

      if (navigate.getParam('profession')) {
        this.profession = navigate.getParam('profession')
      }

      // localStorage.setItem('device_select', '')
      // localStorage.setItem('device_type_select', '')

      this.loadList()
    },
    loadList () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      const type = this.types.length > 0 ? this.types[this.types.length - 1].id : 0

      io.getDevice({
        type: type,
        mode: 'category'
      }).then(data => {
        this.typeList = type == 0 ? data.typeList.filter(item => this.profession ? item.profession == this.profession : true) : data.typeList
        this.deviceList = type == 0 ? data.deviceList.filter(item => this.profession ? item.profession == this.profession : true) : data.deviceList
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    onSearch () {
      if (this.query) {
        this.$toast.loading({
          duration: 0,
          loadingType: 'spinner'
        })

        io.searchDevice({query: this.query}).then(data => {
          this.queryList = data.deviceList
          this.$toast.clear()
        }).catch(msg => {
          this.$toast(msg)
        })
      }
    },

    handleType (item) {
      let typesParam = [...this.types]
      typesParam.push(item)

      let cid = []

      if (navigate.getParam('cid')) {
        cid = navigate.getParam('cid')
      }

      cid.push(item.id)

      navigate.copen('device', item.id, {types: typesParam, radom: (new Date()).getTime(), cid, category: this.showRadio ? '':'false' })
    },

    handleDevice (item) {
      localStorage.setItem('device_select', JSON.stringify(item))
      localStorage.setItem('device_type_select', JSON.stringify({
        id: item.typeId,
        name: item.typeName
      }))

      navigate.back('device')

      if (navigate.getParam('cid')) {
        const cid = navigate.getParam('cid')

        for (let i = 0; i < cid.length; i++) {
          navigate.back('device_'+cid[i])
        }
      }
    },

    handleTypeSelect (item) {
      this.radio = item.id

      localStorage.setItem('device_type_select', JSON.stringify({
        id: item.id,
        name: item.name
      }))

      navigate.back('device')

      if (navigate.getParam('cid')) {
        const cid = navigate.getParam('cid')

        for (let i = 0; i < cid.length; i++) {
          navigate.back('device_'+cid[i])
        }
      }
    },

    onClickLeft () {
      navigate.back()
    }
  },
  watch: {
    query (val) {
      if (val) {
        // this.queryList = []
        this.queryShow = true
      } else {
        this.queryShow = false
      }
    },
    queryShow (val) {
      if (val) {
        this.queryList = []
      }
    }
  }
}
</script>

<style lang="scss">
.list-container{
  .van-cell-group+.van-cell-group {
    margin-top: 12px;
  }

  .user-icon{
    width: 45px;
    height: 45px;
    margin-right: 12px;
    border-radius: 50%;
  }
}
</style>
